.overlay {

    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgb(0, 0, 0, 0.4);
    z-index: 1;
}

.preloader {
    position: absolute;
    top: 40%;
    left: calc(50% - 300px);
    height: 200px;
    width: 600px;
    padding: 50px;
    background-color: #fff;
    z-index: 2;
}

.preloader div {
    text-align: center;
}